.footer{
    background: rgba(23,103,123,0.1);
    padding: 60px 0;
    font-size: px_to_rem(20px);
    color: #7B9CA4;
    .col-md-8{
        padding-left: 15%;
    }
    a{
        color: #7B9CA4;
    }
    .title{
        font-weight: bold;
        color: #17677B;
        font-size: px_to_rem(24px);
    }
    ul{
        padding-left: 0;
        li{
            margin-bottom: 15px;
        }
    }
    .logo{
        display: block;
        margin-bottom: 17px;
    }
    @media (max-width: 767px) {
        font-size: px_to_rem(16px);
        padding: 40px 0 10px 0;
        .logo{
            width: 164px;
        }
        .title{
            font-size: px_to_rem(20px);
        }
        ul{
            margin-bottom: 30px;
        }
        .col-md-4{
            margin-bottom: 30px;
        }
        .col-md-8{
            padding-left: 15px;
        }
    }

}